// 頂尖師資 起點~~~
.top-teachers{
  padding: 32px 0 32px 0;
  &-img{
    display:none;
    max-height: 628px;
    border-radius:$border-radius-xl;
    @include pc {
      width: 50%;
      display:block;
      object-fit: cover;
    }
  }
}

.top-teachers-main{
  @include pc{
    padding-top: 30px;
    padding-left: 30px;
    width: 50%;
  }
  h1{
    margin-bottom: 4px;
    color: $secondary;
    font-size: 28px;
    @include pc{
      font-size: 28px;
    }
  }
  p{
    margin-bottom: 24px;
    color: $primary;
    font-size: 20px;
    @include pad{
      width: 450px;
    }
    @include pc{
      font-size: 16px;
      width: 100%;
    }
  }
}
.top-teachers-header{
  margin-bottom: 0;
  @include pad{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  @include pc{
    align-items: flex-start;
  }
}

.top-teachers-container{
  @include pc{
  display: flex;
  }
}

.top-teachers-cards{
  display: flex;
  flex-direction: column;
  @include pad{
  align-items:flex-end;
  }
  &-one,&-others{
    display: flex;
    align-items: center;
    background: #fff;
    border-radius: 16px;
    padding: 16px;
    margin-bottom:16px;
    ul{
      margin-bottom: 0;
    }
    img{
      max-width: 48px;
      max-height: 48px;
      margin-right: 16px;
    }
    h1{
      font-size:20px;
    }
    p{
      font-size: 16px;
    }
  }
}

@include pad{
  .top-teachers-cards-one{
    width: 630px;
    padding: 40px 52px 40px 48px;
    img{
      max-width: 94px;
      max-height: 94px;
    }
  }
  .top-teachers-cards-others{
    img{
      max-width: 68px;
      max-height: 68px;
    }
    padding: 24px 0 24px 32px;
    width: 540px;
  }
}

@include pc{
  .top-teachers-cards-one{
    width: 118%;
  }
  .top-teachers-cards-others{
    width: 100%;
  }
}

.top-teachers-cards-one-list{
  h1{
    text-align: left;
    margin-bottom: 4px;
    color: $dark;
    font-size: 20px;
  }
  p{
    color: $dark;
    font-size: 16px;
    margin-bottom: 4px;
  }
}

// 頂尖師資 終點~~~

